.teaser.lazyload {
    opacity: 0;
    -webkit-transform: scale(0.8);
        -ms-transform: scale(0.8);
            transform: scale(0.8);
}

.teaser.lazyloaded {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 700ms;
    -o-transition: all 700ms;
    transition: all 700ms;
}


